#game-menu {
	height: 100%;
	.dialog {
		height: 100%;
		overflow-y: auto;
		overflow-x: hidden;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		    -ms-flex-align: center;
		        align-items: center;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
	}
	.items {
		max-height: unset;
		padding: 48px;
	}
	.item {
		padding: 48px 64px;
		background: rgba(255, 255, 255, 0.8);
		text-align: center;
		-webkit-user-select: none;
		   -moz-user-select: none;
		    -ms-user-select: none;
		        user-select: none;
		font-size: 24px;
		color: rgba(0, 0, 0, 0.8);
		-webkit-transition: all .3s;
		transition: all .3s;
		cursor: pointer;
		margin: 24px 0;
		&:hover {
			-webkit-box-shadow: 0 4px 16px 4px rgba(0, 0, 0, 0.2);
			        box-shadow: 0 4px 16px 4px rgba(0, 0, 0, 0.2);
		}
	}
}



